// components
import Container from "./Container";
import swal from "sweetalert2";
import Button from "./Button";
import Input from "./Input";
import Label from "./Label";
import Form from "./Form";
import Alert from '@mui/material/Alert';
import CircularProgress from '@mui/material/CircularProgress';
import Paper from '@mui/material/Paper';

// other imports
import { useSelector, useDispatch } from 'react-redux';
import { registerUsers } from "../action/userActions";
import React, { useState } from "react";
import { useNavigate, Link } from "react-router-dom";

const Register = () => {

  const [username, setUsername] = useState('');
  const [email, setEmail] = useState('');
  const [number, setNumber] = useState('');
  const [password, setPassword] = useState('');

  const dispatch = useDispatch();
  const navigate = useNavigate();

  const registerFetch = useSelector(store => store.register);
  const { loading, data, error } = registerFetch;

  const resetForm = () => {
    setUsername('');
    setEmail('');
    setNumber('');
    setPassword('');
  };

  const redirectToLogin = () => setTimeout(() => { navigate('/login') }, 500);
  
  const submitHandler = async e => {
    e.preventDefault();
    if (username.trim() === '' || email.trim() === '' || number.trim() === '' || password.trim() === '') {
      return swal.fire({
        icon: "error",
        text: "All Fields are required!"
      });
    }
    const formData = {
      username: username,
      email: email,
      number: number,
      password: password
    }
    dispatch(registerUsers(formData));
    resetForm();
  };

  return (
    <Container>
      <Paper elevation={12}
      sx={{width:'30%',marginTop:'5rem',marginX:'auto', padding:'1rem', borderRadius:'5%', border:'10px ridge green'}}>
        <Form>
          <Label htmlFor="username" label="Username" />
          <Input
            onChange={(e) => setUsername(e.target.value)}
            value={username}
            id="username"
            placeholder="Username"
          />
          <Label htmlFor="email" label="Email" />
          <Input
            onChange={(e) => setEmail(e.target.value)}
            value={email}
            id="email"
            placeholder="Email"
          />
          <Label htmlFor="number" label="Phone Number" />
          <Input
            onChange={(e) => setNumber(e.target.value)}
            value={number}
            id="number"
            placeholder="Phone Number"
            type="number"
          />
          <Label htmlFor="password" label="Password" />
          <Input
            onChange={(e) => setPassword(e.target.value)}
            value={password}
            id="password"
            placeholder="Password"
            type="password"
          />
          <Button onClick={submitHandler} val="Register" bg="green" />
          <Link to="/login" style={{width:'min-content',borderBottom:'1px solid green'}}>Login</Link>
          {loading && <CircularProgress/>}
          {data.status && data.status !== 200 ?
            <Alert severity="error">error code {data.status}</Alert> :
            data.status === 200 ? redirectToLogin() : ''}
          {error && <Alert severity="error">error:{error}</Alert>}

        </Form>
      </Paper>
    </Container>
  );
};

export default Register;