import Autocomplete from '@mui/material/Autocomplete';
import TextField from '@mui/material/TextField';
import Tabs from '@mui/material/Tabs';
import Box from '@mui/material/Box';
import AppBar from '@mui/material/AppBar';
import ProfileAvatar from './ProfileAvatar';
import TabContext from '@mui/lab/TabContext';
import Tab from '@mui/material/Tab';
import { useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { useNavigate } from 'react-router-dom';

import { getProfilepic } from "../action/profileActions";
import ProfileMenu from './ProfileMenu';

const Navbar = props => {

  const dispatch = useDispatch();
  const navigate = useNavigate();

  const getpfp = useSelector(store => store.getpfp);
  const { data:pfpData } = getpfp;

  const [navValue, setNavValue] = useState("0");

  const handleChange = (e, newValue) => {
    setNavValue(newValue);
  };

  const top100Films = [
    { title: 'Harry Potter', year: 2022 }
  ];
  useEffect(() => {
    props.id ? dispatch(getProfilepic(props.id)) : dispatch(getProfilepic("default"));
  }, [dispatch, props]);
  return (
    <AppBar className="navbar" color={'default'} enableColorOnDark
      sx={{ borderBottom: 1, borderColor: 'divider'}}>
      <TabContext value={navValue}>
				<Tabs value={navValue} onChange={handleChange} sx={{width:'100%'}}>

						<Tab icon={<img src="https://indra.amjilt.com/imagesSchool/logo-school.png" alt="logo" width="150px"/>}
							value="0" onClick={e=>navigate('/')}/>
						<Tab value="1" label="Create Post" href='/create'/>
						<Tab value="2" label="All Posts" href='/'/>
						<Tab value="3" label="Anime" href='/anime'/>
						<Tab value="4" label="Cars" href='/cars'/>

						<Box className="navInput" component="form" autoComplete="off"
            sx={{ gridColumn: '11 / 14', width:'25%', marginRight:3, marginLeft:17}}>
							<Autocomplete freeSolo autoHighlight
								onChange={(e, value) => console.log(value)}
								options={top100Films.map((option) => option.title)}
								renderInput={(params) => <TextField {...params} label="Search Posts" onChange={(e) => { e.stopPropagation()}}
								/>} />
						</Box>
						{
							pfpData && pfpData ? <ProfileMenu id={props.id}>
                <ProfileAvatar alt="Profile" src={pfpData.message} className="profilePic" sx={{ gridColumn: '15 / 15' }}/>
              </ProfileMenu> : <ProfileMenu id={props.id}>
							  <ProfileAvatar alt="Profile" src="/build/temp.png" className="profilePic" sx={{ gridColumn: '15 / 15' }}/>
              </ProfileMenu>
            }
				</Tabs>
      </TabContext>
    </AppBar>
  )

}

export default Navbar;