import * as actionTypes from '../constants/profileConstants';

export const getProfilePic = (state = {}, action) => {
    switch (action.type) {
        case actionTypes.GET_PROFILE_PIC_RES:
            return {data: action.payload};
        case actionTypes.GET_PROFILE_ERROR:
            return {error: action.message}
        default: return state
    }
}