import Avatar from '@mui/material/Avatar';
import axios from 'axios';
import { useState } from 'react';

const EditedAvatar = props =>{
    const getpfp = async(id)=>{
        const {data} = await axios.get(`https://biy-daalt-unnamed.herokuapp.com/profilePic/${id}`);
        setSrc(data.message);
    }
    const [src, setSrc] = useState('');
    getpfp(props.src);
    return (
    src ? <Avatar src={src}/> : <Avatar src={src}/>
    )
    // const src = getpfp(props.src)
}

export default EditedAvatar;