import * as actionTypes from '../constants/profileConstants';
import axios from 'axios';

export const getProfilepic = (id) => async (dispatch) => {
    try {
        const {data} = await axios.get(`https://biy-daalt-unnamed.herokuapp.com/profilePic/${id}`);
        dispatch({
            type: actionTypes.GET_PROFILE_PIC_RES,
            payload: data
        })
    } catch (error){
        dispatch({
            type: actionTypes.GET_PROFILE_ERROR,
            message: error.message
        })
    }
}