import * as actionTypes from '../constants/postConstants';
import axios from 'axios';

export const getPosts = () => async (dispatch) => {
    try {
        dispatch({
            type: actionTypes.GET_POSTS_REQ
        });
        const response = await axios.get('https://biy-daalt-unnamed.herokuapp.com/postGet');
        dispatch({
            type: actionTypes.GET_POSTS_RES,
            status: response.status,
            payload: response.data,
        });
    } catch (error){
        dispatch({
            type: actionTypes.GET_POSTS_ERROR,
            message: error.message
        });
    };
};

export const createPost = formData => async (dispatch) => {
    try {
        dispatch({
            type: actionTypes.CREATE_POSTS_REQ
        });
        const response = await axios.post('https://biy-daalt-unnamed.herokuapp.com/post', formData);
        dispatch({
            type: actionTypes.CREATE_POSTS_RES,
            status: response.status,
            payload: response.data,
        });
    } catch (error){
        dispatch({
            type: actionTypes.CREATE_POSTS_ERROR,
            message: error.message
        });
    };
};