import { useState } from "react";
import Container from "./Container";
import Form from "./Form";
import Label from "./Label";
import Input from "./Input";
import Button from "./Button";
import swal from "sweetalert2";
import Alert from '@mui/material/Alert';

import { useNavigate, Link } from "react-router-dom";
import { loginUsers } from "../action/userActions";
import { useSelector, useDispatch } from 'react-redux';
import CircularProgress from '@mui/material/CircularProgress';
import Paper from '@mui/material/Paper';

const Login = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const loginData = useSelector(state => state.login);
  const { loading, data, status, error } = loginData;

  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');

  // useEffect(() => {
  //     const getUserFromLocalStorage = async ()=>{
  //     const data = getfromlocalstorage
  //     if(data){
  //         dosmth
  //     };
  //     
  //     getUserFromLocalStorage();
  // }, []);


  const submitHandler = async (e) => {
    e.preventDefault();
    if (email.trim() === '' || password.trim() === '') {
      return swal.fire({
        icon: "error",
        text: "All Fields are Required!",
      });
    };
    const formData = {
      email: email,
      password: password
    };
    dispatch(loginUsers(formData));
    resetForm();
  };
  const resetForm = () => {
    setEmail('');
    setPassword('');
  };
  const redirectToHome = () => {
    setTimeout(() => { navigate('/') }, 500)
  }

  return (
    <Container>
      <Paper elevation={12}
        sx={{ width: '30%', marginTop: '5rem', marginX: 'auto', padding: '1rem', borderRadius: '5%' }}>
        <Form onSubmit={submitHandler}>
          <Label htmlFor="email" label="Email" />
          <Input
            onChange={(e) => setEmail(e.target.value)}
            value={email}
            id="email"
            placeholder="Email"
          />
          <Label htmlFor="password" label="Password" />
          <Input
            onChange={(e) => setPassword(e.target.value)}
            value={password}
            id="password"
            placeholder="Password"
            type="password"
          />
          <Button val={"Login"} bg="blue" />
          <Link to="/register" style={{ width: 'min-content', borderBottom: '1px solid green' }}>Register</Link>
          {loading && <CircularProgress />}
          {data ? localStorage.setItem('currentUser', data.user._id) : ''}
          {data && status !== 200 ?
            <Alert severity="error">error code {data.status}</Alert> :
            status === 200 ? redirectToHome() : ''}
          {error && <Alert severity="error">error:{error}</Alert>}
        </Form>
      </Paper>
    </Container>
  );
};

export default Login;