import { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { getPosts } from '../action/postActions';
import Skeleton from '@mui/material/Skeleton';

import Card from '@mui/material/Card';
import CardHeader from '@mui/material/CardHeader';
import CardContent from '@mui/material/CardContent';
// import CardMedia from '@mui/material/CardMedia';
import CardActions from '@mui/material/CardActions';
import ShareIcon from '@mui/icons-material/Share';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import MoreVertIcon from '@mui/icons-material/MoreVert';
import EditedAvatar from './EditedAvatar';

const LoadPosts = (props) => {

  const dispatch = useDispatch();

  const getPostsStatus = useSelector(store => store.getPostsStatus);
  const { loading, data } = getPostsStatus;
  // const loggedUser = props.id
  useEffect(() => {
    return dispatch(getPosts());
  }, [dispatch])
  return (
    <div style={{ display: 'grid',gridTemplateColumns: '3fr auto', gridColumnGap:'50px'}}>
      {loading && (<>
        <Skeleton variant="circular" width={40} height={40} />
        <Skeleton variant="rectangular" width={170} sx={{ my: 1 }} />
        <Skeleton variant="rectangular" width={210} height={118} />
      </>)}
      {data &&
        // console.log(data)
        data.message.map((value, i) => {
          return <Card sx={{ maxWidth: 400, marginBottom:5 }} key={`post-${i}`}>
            <CardHeader
              avatar={<EditedAvatar src={value.Poster_id}></EditedAvatar>}
              action={
                <IconButton>
                  <MoreVertIcon />
                </IconButton>}
              title={value.Poster_id}
            // subheader="date and time"
            />

            {/* <CardMedia
              component="img"
              height="194"
              image="/static/images/cards/paella.jpg"
              alt="photo" /> */}
            <CardContent>
              <Typography variant="body2" color="text.secondary">{value.text}</Typography>
            </CardContent>
            <CardActions disableSpacing>
              <IconButton aria-label="share"><ShareIcon/></IconButton>
            </CardActions>
          </Card>
        })

      }
    </div>
  )

}

export default LoadPosts;
