import React from 'react';
import ReactDOM from 'react-dom';
import { HashRouter, Routes, Route } from "react-router-dom";
import Register from './components/Register';
import Login from './components/Login';
import App from './components/App';
import Profile from './components/Profile';
import Err404 from './components/404';

import { Provider } from 'react-redux';
import store from './store';
import './App.css';
import 'dotenv/config';

const root = document.getElementById('root');

ReactDOM.render(
  <HashRouter>
    <Provider store={store}>
      <Routes>
        <Route exact path={process.env.PUBLIC_URL + '/*'} element={<App />}/>
        <Route exact path={process.env.PUBLIC_URL + '/register'} element={<Register />} />
        <Route exact path={process.env.PUBLIC_URL + '/login'} element={<Login />} />
        <Route exact path={process.env.PUBLIC_URL + '/profile'} element={<Profile />} />
        <Route path="*" element={<Err404 to='/login' text='Login'/>} />
      </Routes>
    </Provider>
  </HashRouter>,
  root
);