import { useState, useEffect } from 'react';
import CircularProgress from '@mui/material/CircularProgress';
import { useSelector, useDispatch } from 'react-redux';
import { createPost } from "../action/postActions";
import Button from '@mui/material/Button';
import Input from '@mui/material/Input';
import Alert from '@mui/material/Alert';
import MenuItem from '@mui/material/MenuItem';
import Select from '@mui/material/Select';

const Create = props => {

  const [post, setPost] = useState('');
  const [btnDisabled, setBtnDisabled] = useState(true);
  const [selectVal, setSelectVal] = useState(0);
  const [loggedStatus, setLogged] = useState(null);

  const dispatch = useDispatch();

  const postingStatus = useSelector(store => store.postingStatus);
  const { loading, data, error } = postingStatus;
  const loggedUser = props.id
  useEffect(() => {
    if (!props.id)return setLogged(false)
    setLogged(true);
    if (post.trim() !== '') return setBtnDisabled(false);
    setBtnDisabled(true);
    
  }, [post,props])

  const postSubmit = () => {
    if (post.trim() === '') return;
    const formData = {
      value: post,
      _id: loggedUser
    }
    dispatch(createPost(formData));
  };

  const setSelect = e => {
    setSelectVal(e.target.value);
  };

  return (
    <div style={{ display: 'grid', placeItems: 'center' }}>
    {!loggedStatus && <Alert severity="error">Login to Post</Alert>}
      <Select value={selectVal}
        label="Type"
        onChange={setSelect}>
        <MenuItem value={0}>General</MenuItem>
        <MenuItem value={1}>Anime</MenuItem>
        <MenuItem value={2}>Other</MenuItem>
      </Select>
      <Input placeholder="Placeholder" value={post} onChange={e => setPost(e.target.value)} /><br />
      <Button variant="outlined" onClick={postSubmit}
        disabled={btnDisabled}>Submit</Button>
      {loading && <CircularProgress />}
      {data && console.log('a', data)}
      {error && <Alert severity="error">error:{error}</Alert>}
    </div>
  )

}

export default Create;