const regState = {
    loading: false,
    data: {}
};

export const registerUsers = (state = regState, action) => {
    switch (action.type) {
        case actionTypes.REG_USERS_REQ:
            return {
                ...state,
                loading: true
            }
        case actionTypes.REG_USERS_RES:
            return {
                ...state,
                loading: false,
                status: action.status,
                data: action.payload
            };
        case actionTypes.REG_USERS_ERROR:
            return {
                error: action.message
            }
        default: return state
    }
}

export const loginUsers = (state = {}, action) => {
    switch (action.type) {
        case actionTypes.LOGIN_USERS_REQ:
            return {
                ...state,
                loading: true
            }
        case actionTypes.LOGIN_USERS_RES:
            return {
                ...state,
                loading: false,
                status: action.status,
                data: action.payload
            };
        case actionTypes.LOGIN_USERS_ERROR:
            return {
                error: action.message
            }
        default: return state
    }
}