import Footer from './Footer';
import Navbar from './Navbar';
import React, { useState, useEffect } from 'react';
import { Routes, Route } from "react-router-dom";
import { ThemeProvider, createTheme } from '@mui/material/styles';
import Paper from '@mui/material/Paper';
// import CircularProgress from '@mui/material/CircularProgress';
import LoadPosts from './LoadPosts';
import Create from './CreatePost';
import Err404 from './404';
import 'dotenv/config';

const App = () => {

  const [loggedUser, setLoggedUser] = useState('');

  useEffect(() => {
    const user = localStorage.getItem('currentUser')
    if (!user) return;
    setLoggedUser(user);
  }, []);


  const theme = React.useMemo(
    () => createTheme({
      palette: {
        primary: {
          main: '#000',
        },
        mode: 'dark'
      },
      components: {
        MuiInputLabel: {
          styleOverrides: {
            root: {
              "&.Mui-focused": {
                color: 'whitesmoke',
              },
            },
          }
        }
      }
    }), []
  );


  return (
    <>
      <ThemeProvider theme={theme}>
        <Navbar id={loggedUser} />
        <Paper sx={{ marginTop: '65px', height: '90vh', display: 'grid', placeItems: 'center' }}>
          <Routes>
            <Route path={process.env.PUBLIC_URL + '/'} element={<LoadPosts/>} />
            <Route path={process.env.PUBLIC_URL + '/create'} element={<Create id={loggedUser} />} />
            <Route path={process.env.PUBLIC_URL + '/general'} element={<div />} />
            <Route path={process.env.PUBLIC_URL + '/anime'} element={<div />} />
            <Route path={process.env.PUBLIC_URL + '/cards'} element={<div />} />
            <Route path="*" element={<Err404 to={'/'} text={'Homepage'} />} />
          </Routes>
        </Paper>
        <Footer />
      </ThemeProvider>
    </>

  );
};

export default App;