import Container from '@mui/material/Container';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import Link from '@mui/material/Link';

const Footer = () => {
  return (
    <footer>
      <Box
        px={{ xs: 3, sm: 10 }}
        py={{ xs: 3, sm: 5 }}
        color="white"
        sx={{bgcolor: 'primary.main'}}>
        <Container maxWidth="lg">
          <Grid container spacing={5}>
            
            <Grid item xs={12} sm={4}>
              <Box borderBottom={1}>Help</Box>
              <Box>
                <Link href="https://bit.ly/3dp3FDu" target="_blank" color="inherit">Contact</Link>
              </Box>
              <Box>
                <Link href="https://bit.ly/3dp3FDu" color="inherit">Support</Link>
              </Box>
              <Box>
                <Link href="/" color="inherit">Privacy Policy</Link>
              </Box>
            </Grid>

            <Grid item xs={12} sm={4}>
              <Box borderBottom={1}>Account</Box>
              <Box>
                <Link href="/login" color="inherit">Login</Link>
              </Box>
              <Box>
                <Link href="/register" color="inherit">Register</Link>
              </Box>
            </Grid>

            <Grid item xs={12} sm={4}>
              <Box borderBottom={1}>Messages</Box>
              <Box>
                <Link href="/" color="inherit">Backup</Link>
              </Box>
              <Box>
                <Link href="/" color="inherit">History</Link>
              </Box>
              <Box>
                <Link href="/" color="inherit">Roll</Link>
              </Box>
            </Grid>

          </Grid>
          <Box textAlign="center" pt={{ xs: 5, sm: 5 }} pb={{ xs: 5, sm: 0 }}>
            Gantulga Tengis &reg; 2021
          </Box>
        </Container>
      </Box>
    </footer>
  );
};

export default Footer;