import * as actionTypes from '../constants/postConstants';

const regState = {
    loading: false
};

export const createPost = (state = regState, action) => {
    switch (action.type) {
        case actionTypes.CREATE_POSTS_REQ:
            return {
                ...state,
                loading: true
            }
        case actionTypes.CREATE_POSTS_RES:
            return {
                ...state,
                status: action.status,
                loading: false,
                data: action.payload
            };
        case actionTypes.CREATE_POSTS_ERROR:
            return {
                error: action.message
            }
        default: return state
    }
}

export const getPosts = (state = regState, action) => {
    switch (action.type) {
        case actionTypes.GET_POSTS_REQ:
            return {
                ...state,
                loading: true
            }
        case actionTypes.GET_POSTS_RES:
            return {
                ...state,
                status: action.status,
                loading: false,
                data: action.payload
            };
        case actionTypes.GET_POSTS_ERROR:
            return {
                error: action.message
            }
        default: return state
    }
}